/*Section heading style-1*/

.section__heading {
    h2 {
        color: $cloudburst;
        font-size: 34px;
        font-weight: 600;
        font-style: normal;
        letter-spacing: -1px;
        line-height: 44px;
        margin-bottom: 25px;

        @include mobile {
            font-size: 38px;
            line-height: 48px;
        }

        @include tablet {
            font-size: 48px;
            line-height: 58px;
        }
    }

    p {
        opacity: 0.7;
        color: $cloudburst;
        font-size: 17px;
        font-weight: 400;
        font-style: normal;
        letter-spacing: normal;
        line-height: 30px;
        margin-bottom: 0;
    }
}

/*Section heading style-2*/

.section__heading-2 {
    h2 {
        color: $mirage;
        font-size: 34px !important;
        font-weight: 600;
        font-style: normal;
        letter-spacing: -1px;
        line-height: 42px;
        margin-bottom: 30px;

        @include mobile {
            font-size: 38px !important;
            line-height: 45px;
        }

        @include tablet {
            font-size: 48px;
            line-height: 55px;
        }
        @include desktops {
            font-size: 60px !important;
            line-height: 66px;
        }
    }

    p {
        opacity: 0.8;
        color: $mirage;
        font-size: 17px;
        font-weight: 400;
        font-style: normal;
        line-height: 30px;
        margin-bottom: 0;
    }
}

.section__heading-3 {
    h2 {
        color: $mirage;
        font-size: 34px !important;
        font-weight: 600;
        font-style: normal;
        letter-spacing: -1px;
        line-height: 42px;
        margin-bottom: 30px;

        @include mobile {
            font-size: 38px !important;
            line-height: 45px;
        }

        @include tablet {
            font-size: 42px;
            line-height: 50px;
        }
        
    }

    p {
        opacity: 0.7;
        color: $mirage;
        font-size: 15px;
        font-weight: 400;
        font-style: normal;
        line-height: 28px;
        margin-bottom: 0;
    }
}